<template>
    <section class="subscribe-newsletter text-center py-12 bg-gray-100">
        <div class="py-12">
      <h2 class="text-4xl font-regular mb-4">Subscribe to Our Newsletter</h2>
      
      <form @submit.prevent="handleSubmit" class="flex justify-center">
    <div class="border-2 border-black py-1 px-2 w-1/3 flex items-center">
      <input 
        type="email" 
        v-model="email" 
        placeholder="Enter your email" 
        required 
        class="rounded-l py-3 px-4 w-full border-0 focus:outline-none" 
      />
      <button 
        type="submit" 
        class="rounded-r py-3 px-4 flex items-center justify-center"
        aria-label="Submit Email"
      >
        <svg 
          xmlns="http://www.w3.org/2000/svg" 
          class="h-6 w-6" 
          fill="none" 
          viewBox="0 0 24 24" 
          stroke="currentColor" 
          aria-hidden="true"
        >
          <path 
            stroke-linecap="round" 
            stroke-linejoin="round" 
            stroke-width="2" 
            d="M13 7l5 5-5 5M6 12h12" 
          />
        </svg>
      </button>
    </div>
  </form>
</div>
      <p v-if="submitted" class="mt-4 text-green-500">Thank you for subscribing!</p>
    </section>
  </template>
  
  <script>
  export default {
    name: 'SubscribeNewsletter',
    data() {
      return {
        email: '',
        submitted: false,
      };
    },
    methods: {
      handleSubmit() {
        // Here you can add logic to handle the subscription (e.g., sending email to your server)
        console.log('Subscribed with email:', this.email);
  
        // Reset the email input and show confirmation message
        this.email = '';
        this.submitted = true;
  
        // Optional: Hide the confirmation message after a few seconds
        setTimeout(() => {
          this.submitted = false;
        }, 3000);
      },
    },
  };
  </script>
  
  <style scoped>
  .subscribe-newsletter {
    background-color: #f9fafb; /* Light gray background */
  }
  </style>
  