<template>
  <div class="slider-container">
    <div
      class="slider"
      :style="{ transform: `translateX(-${currentIndex * (100 / itemsPerPage)}%)` }"
    >
      <div
        v-for="(item, index) in items"
        :key="index"
        class="slide"
        :class="{ dull: index === currentIndex + 0 }" 
      >
        <a :href="item.link" target="_blank" class="slide-content">
          <img :src="item.image" :alt="item.title" />
          <div class="caption">
            <h3>{{ item.title }}</h3>
          </div>
        </a>
      </div>
    </div>
  </div>
  <div class="navigation-arrows">
      <button class="prev" @click="prevSlide">❮</button>
      <button class="next" @click="nextSlide">❯</button>
    </div>
</template>

<script>
export default {
  props: {
    items: {
      type: Array,
      required: true,
    },
  },
  data() {
    return {
      currentIndex: 0,
      itemsPerPage: 3.5, // Display 3.5 images per view
    };
  },
  computed: {
    maxIndex() {
      return Math.ceil(this.items.length - this.itemsPerPage);
    },
  },
  methods: {
    nextSlide() {
      if (this.currentIndex < this.maxIndex) {
        this.currentIndex += 1;
      } else {
        this.currentIndex = 0; // Loop back to the start
      }
    },
    prevSlide() {
      if (this.currentIndex > 0) {
        this.currentIndex -= 1;
      } else {
        this.currentIndex = this.maxIndex; // Loop to the end
      }
    },
  },
};
</script>

<style scoped>
.slider-container {
  position: relative;
  overflow: hidden;
  width: 100%;
  margin: 0 auto;
  display: flex;
  padding: 0 0 0 14.4rem;
  align-items: center;
}

.slider {
  display: flex;
  transition: transform 0.5s ease-in-out; /* Smooth transition for silk effect */
}

.slide {
  padding: 0 10px; /* Padding between slides */
  box-sizing: border-box;
  display: flex;
  align-items: center;
  position: relative;
}

.slide.dull {
 
}

.slide-content {
  position: relative;
  text-align: center;
}

img {
  height: 22rem;
  object-fit: cover; /* Ensures the image covers the space without distortion */
  transition: transform 0.3s ease; /* Smooth transition for hover effect */
}

img, video {
  max-width: none !important;
  height: auto;
}


.caption {
  position: absolute;
  bottom: 0;
  background: black; /* Dark overlay */
  color: white;
  width: 100%;
  text-align: center;
  padding: 10px 0;
  font-size: 18px; /* Increased font size for readability */
}

/* Move arrows to the bottom and center them */
.navigation-arrows {
  display: flex;
  justify-content: center;
  margin-top: 20px;
}

button.prev,
button.next {
  background-color: white;
  border: none;
  cursor: pointer;
  padding: 15px;
  border-radius: 68%;
  box-shadow: 1px 1px 18px rgba(0, 0, 0, 0.3);
  margin: 0 10px;
  transition: background-color 0.3s;
  width: 3.3rem;
}

button.prev:hover,
button.next:hover {
  background-color: #f0f0f0;
}

/* CTA button below the slider */
.cta-button {
  margin-top: 20px;
  padding: 10px 20px;
  background-color: #900;
  color: white;
  border: none;
  border-radius: 5px;
  font-size: 16px;
  cursor: pointer;
  transition: background-color 0.3s; /* Smooth transition for hover effect */
}

.cta-button:hover {
  background-color: #b00;
}

/* Make the slider responsive for smaller screens */
@media (max-width: 768px) {
  .slide {
    width: calc(100% / 1.5); /* Show 1.5 images per view on mobile */
  }

  button.prev,
  button.next {
    padding: 10px;
  }

}
</style>
